import forumobjct from "@/components/script/forumobjct";
import videoobject from "@/components/script/videoobject";
import video_index_recommend from "@/components/video/index/recommend";

export default {
  name: "index",
  mixins: [forumobjct, videoobject],
  components: {
    video_index_recommend
  },
  data() {
    return {
      video_class_name: '',
      show_what: undefined,
      show_node_dialog: false,
      recommend_javs:undefined
    }
  },
  destroyed() {
    this.listener_init.off();
  },
  created() {
    window._t = this;
    console.log('create topics index');
    this.init();
    this.listener_init = this.$hub.on('event:init', () => {
      console.log('call event:init');
      this.init();
      this.$forceUpdate();
    });
    this.video_class_name = this.video_col_class_name();
    console.log('this.video_class_name:', this.video_class_name);
  },
  methods: {
    on_click_show_node: function () {
      console.log('this.show_node_dialog:', this.show_node_dialog);
      this.show_node_dialog = true;
      this.$forceUpdate();
    },
    on_select_forum: function (forum) {
      var forum_url = '/forum/' + forum.fid;
      this.$router.push(forum_url);
    },
    init: function () {
      this.update_threads();
      if (this.carousel_threads && this.carousel_threads.length > 0) {
        this.slide = this.carousel_threads[0]['id'];
      }
    },
    update_threads: function () {
      this.last_threads = this.get_last_threads();
      this.indextop_threads = this.get_indextop_threads();
      this.carousel_threads = this.get_carousel_threads();
      this.recommend_javs = this.get_recommend_javs();
      console.log('recommend_javs:', this.recommend_javs);
    },
    get_last_threads: function () {
      try {
        if (this.$store.state.init.data.last_threads.datas.length > 0) {
          return this.$store.state.init.data.last_threads.datas;
        }
      } catch (e) {

      }
      return undefined
    },
    get_indextop_threads: function () {
      try {
        if (this.$store.state.init.data.indextop_threads.datas.length > 0) {
          return this.$store.state.init.data.indextop_threads.datas;
        }
      } catch (e) {

      }
      return undefined
    },
    get_carousel_threads: function () {
      try {
        if (this.$store.state.init.data.carousel_threads.datas.length > 0) {
          return this.$store.state.init.data.carousel_threads.datas;
        }
      } catch (e) {

      }
      return undefined
    },
    get_recommend_javs: function () {
      try {
        if (this.$store.state.init.data.recommend_javs.datas.length > 0) {
          var recommend_javs_datas = this.$store.state.init.data.recommend_javs.datas;
          if(recommend_javs_datas.length > 8){
            recommend_javs_datas = recommend_javs_datas.slice(0,8)
          }
          return recommend_javs_datas;
        }
      } catch (e) {

      }
      return undefined
    }
  },
}
